export default class CreateUrl {
  constructor(searchString) {
    this.searchString = searchString;
    this.apiKey = 'AIzaSyCPboMRQxsdw0NWMLdlwsaAeAm0ZhhrZnk';
    this.basicUrl = 'https://www.googleapis.com/youtube/v3/';
  }

  getUrl(maxResults) {
    return `${this.basicUrl}search?key=${this.apiKey}&type=video&part=snippet&maxResults=${maxResults}&q=${this.searchString.match(/\S+/gi).join('+')}`;
  }
}
