export default class AppView {
  static addTagsToHead() {
    const metaResponce = document.createElement('meta');
    metaResponce.name = 'viewport';
    metaResponce.content = 'width=device-width, initial-scale=1, user-scalable=no';
    document.head.appendChild(metaResponce);

    const metaApple = document.createElement('meta');
    metaApple.name = 'apple-mobile-web-app-capable';
    metaApple.content = 'yes';
    document.head.appendChild(metaApple);

    const appleStatusBar = document.createElement('meta');
    appleStatusBar.name = 'apple-mobile-web-app-status-bar-style';
    appleStatusBar.content = 'default';
    document.head.appendChild(appleStatusBar);

    const linkFontAwesome = document.createElement('link');
    linkFontAwesome.rel = 'stylesheet';
    linkFontAwesome.href = 'https://use.fontawesome.com/releases/v5.8.2/css/all.css';
    linkFontAwesome.integrity = 'sha384-oS3vJWv+0UjzBfQzYUhtDYW+Pj2yciDJxpsK1OYPAYjqT085Qq/1cq5FLXAZQ7Ay';
    linkFontAwesome.setAttribute('crossorigin', 'anonymous');
    document.head.appendChild(linkFontAwesome);
  }

  static drawBasicView() {
    const wrapper = document.createElement('div');
    wrapper.className = 'wrapper';

    const serachBoxWrapper = document.createElement('div');
    serachBoxWrapper.className = 'search-box-wrapper';

    const searchBoxInput = document.createElement('input');
    searchBoxInput.className = 'search-box-input';
    searchBoxInput.type = 'text';
    searchBoxInput.placeholder = 'What did you want to find?';

    document.body.appendChild(wrapper);
    wrapper.appendChild(serachBoxWrapper);
    serachBoxWrapper.appendChild(searchBoxInput);


    const clipContainer = document.createElement('ul');
    clipContainer.className = 'clip-container';
    wrapper.appendChild(clipContainer);

    const errorBlock = document.createElement('div');
    errorBlock.className = 'error-block';
    wrapper.appendChild(errorBlock);

    const navigationBar = document.createElement('div');
    navigationBar.className = 'navigation-bar';
    navigationBar.classList.add('-hidden');

    const buttonPrev = document.createElement('button');
    buttonPrev.className = 'button button-prev';
    const buttonPrevTip = document.createElement('div');
    buttonPrevTip.className = 'button-tip-prev';
    buttonPrev.appendChild(buttonPrevTip);

    const buttonCurrent = document.createElement('button');
    buttonCurrent.className = 'button button-current';

    const buttonNext = document.createElement('button');
    buttonNext.className = 'button button-next';
    const buttonNextTip = document.createElement('div');
    buttonNextTip.className = 'button-tip-next';
    buttonNext.appendChild(buttonNextTip);

    navigationBar.appendChild(buttonPrev);
    navigationBar.appendChild(buttonCurrent);
    navigationBar.appendChild(buttonNext);
    wrapper.appendChild(navigationBar);
    document.querySelectorAll('button').forEach(item => item.classList.add('-hidden'));
  }

  static renderItems(data) {
    // Create div for watching on css media queries.
    const mediaCheck = document.createElement('div');
    mediaCheck.className = 'media-check';
    document.body.appendChild(mediaCheck);

    data.then((database) => {
      const arr = database;

      let pageNumber = 0;
      let cardsOnPage;
      let maxPages;
      let leftElementOnPage = 1;
      let oldLeftElem = 1;
      const slider = document.querySelector('.clip-container');
      let isDown = false;
      let startX;
      let direction;
      let allElems = arr.length;

      // function for calculating maximum page number depending on screen size.
      const maxPagesCalc = () => {
        if (getComputedStyle(document.querySelector('.media-check')).width === '1200px') {
          cardsOnPage = 4;
          maxPages = Math.ceil(allElems / cardsOnPage);
        } else if (getComputedStyle(document.querySelector('.media-check')).width === '1024px') {
          cardsOnPage = 3;
          maxPages = Math.ceil(allElems / cardsOnPage);
        } else if (getComputedStyle(document.querySelector('.media-check')).width === '768px') {
          cardsOnPage = 2;
          maxPages = Math.ceil(allElems / cardsOnPage);
        } else if (getComputedStyle(document.querySelector('.media-check')).width === '480px') {
          cardsOnPage = 1;
          maxPages = Math.ceil(allElems / cardsOnPage);
        }
      };

      const addButtonNumber = () => {
        document.querySelector('.button-current').innerHTML = pageNumber + 1;
        document.querySelector('.clip-container').style = `--clips-total: ${allElems}; --page: ${pageNumber};  --direction: 0rem`;
        if (document.querySelector('.button-current').innerText > 1) {
          document.querySelector('.button-prev').classList.remove('-hidden');
          if (pageNumber === maxPages - 1) {
            document.querySelector('.button-next').classList.add('-hidden');
          } else if ((pageNumber < maxPages - 1) && document.querySelector('.button-next').classList.contains('-hidden')) {
            document.querySelector('.button-next').classList.remove('-hidden');
          }
        } else {
          document.querySelector('.button-prev').classList.add('-hidden');
        }
        // add to div for watching on media queries number of actual left card on page,
        // and change border size, depending on this number, for MutationObserver in App.js.
        const target = document.querySelector('.media-check');
        target.innerHTML = leftElementOnPage;
        target.style.border = `${0.01 * leftElementOnPage}px`;
        if ((getComputedStyle(target).width === '1200px' && target.innerHTML === '5' && document.querySelector('.clip-container').children.length === 15)
          || (getComputedStyle(target).width === '1024px' && target.innerHTML === '7' && document.querySelector('.clip-container').children.length === 15)
          || (getComputedStyle(target).width === '768px' && target.innerHTML === '11' && document.querySelector('.clip-container').children.length === 15)
          || (getComputedStyle(target).width === '480px' && target.innerHTML === '13' && document.querySelector('.clip-container').children.length === 15)) {
          allElems = 30;
          maxPagesCalc();
        } else if ((getComputedStyle(target).width === '1200px' && target.innerHTML === '21' && document.querySelector('.clip-container').children.length === 30)
          || (getComputedStyle(target).width === '1024px' && target.innerHTML === '25' && document.querySelector('.clip-container').children.length === 30)
          || (getComputedStyle(target).width === '768px' && target.innerHTML === '25' && document.querySelector('.clip-container').children.length === 30)
          || (getComputedStyle(target).width === '480px' && target.innerHTML === '27' && document.querySelector('.clip-container').children.length === 30)) {
          allElems = 45;
          maxPagesCalc();
        }
      };

      const down = (e) => {
        direction = 0;
        if (e.target.tagName !== 'A') {
          startX = e.pageX;
          isDown = true;
          slider.classList.add('moving');
        } else;
      };

      const touchDown = (e) => {
        direction = 0;
        if (e.path[0].tagName !== 'A') {
          startX = e.changedTouches[0].screenX;
          isDown = true;
          slider.classList.add('moving');
        } else;
      };

      const leave = (e) => {
        if (isDown) {
          isDown = false;
          slider.classList.remove('moving');
          e.preventDefault();
          const endX = e.pageX;
          direction = endX - startX;
          if (pageNumber === 0 && direction < 0) {
            pageNumber = 1;
            leftElementOnPage += cardsOnPage;
          } else if (pageNumber !== 0 && direction < 0 && (pageNumber + 1) !== maxPages) {
            pageNumber += 1;
            leftElementOnPage += cardsOnPage;
          } else if (pageNumber !== 0 && direction > 0) {
            pageNumber -= 1;
            leftElementOnPage -= cardsOnPage;
          } else;
          addButtonNumber();
        }
      };

      const move = (e) => {
        if (!isDown) return;
        e.preventDefault();
        const endX = e.pageX;
        direction = endX - startX;
        document.querySelector('.clip-container').style = `--clips-total: ${allElems}; --page: ${pageNumber};  --direction: ${direction}px`;
      };

      const touchMove = (e) => {
        if (!isDown) return;
        e.preventDefault();
        const endX = e.changedTouches[0].screenX;
        direction = endX - startX;
        document.querySelector('.clip-container').style = `--clips-total: ${allElems}; --page: ${pageNumber};  --direction: ${direction}px`;
      };

      const buttonUp = () => {
        isDown = false;
        if (pageNumber === 0 && direction < 0) {
          pageNumber = 1;
          leftElementOnPage += cardsOnPage;
        } else if (pageNumber !== 0 && direction < 0 && (pageNumber + 1) !== maxPages) {
          pageNumber += 1;
          leftElementOnPage += cardsOnPage;
        } else if (pageNumber !== 0 && direction > 0) {
          pageNumber -= 1;
          leftElementOnPage -= cardsOnPage;
        } else;
        addButtonNumber();
        slider.classList.remove('moving');
      };
      // recalculating of left card on page depending on cads amount on page
      const cardsRecalculate = (oldCardsOnPage) => {
        if (oldCardsOnPage !== cardsOnPage) {
          oldLeftElem = leftElementOnPage;
          const newPage = oldLeftElem / cardsOnPage;
          if (oldLeftElem % cardsOnPage === 0) {
            leftElementOnPage = (newPage - 1) * cardsOnPage + 1;
          } else {
            leftElementOnPage = (Math.floor(newPage)) * cardsOnPage + 1;
          }
          maxPages = Math.ceil(allElems / cardsOnPage);
          pageNumber = Math.ceil(newPage) - 1;
          if (newPage > 0) {
            addButtonNumber();
          } else;
        }
      };

      const resize = () => {
        if (getComputedStyle(document.querySelector('.media-check')).width === '1200px') {
          const oldCardsOnPage = cardsOnPage;
          cardsOnPage = 4;
          cardsRecalculate(oldCardsOnPage);
        } else if (getComputedStyle(document.querySelector('.media-check')).width === '1024px') {
          const oldCardsOnPage = cardsOnPage;
          cardsOnPage = 3;
          cardsRecalculate(oldCardsOnPage);
        } else if (getComputedStyle(document.querySelector('.media-check')).width === '768px') {
          const oldCardsOnPage = cardsOnPage;
          cardsOnPage = 2;
          cardsRecalculate(oldCardsOnPage);
        } else if (getComputedStyle(document.querySelector('.media-check')).width === '480px') {
          const oldCardsOnPage = cardsOnPage;
          cardsOnPage = 1;
          cardsRecalculate(oldCardsOnPage);
        }
      };
      const nextPageClick = () => {
        if (pageNumber < maxPages - 1) {
          pageNumber += 1;
          leftElementOnPage += cardsOnPage;
          document.querySelector('.button-tip-next').classList.add('-visible');
          document.querySelector('.button-tip-next').innerHTML = pageNumber + 2;
          addButtonNumber();
          setTimeout(() => document.querySelector('.button-tip-next').classList.remove('-visible'), 100);
        } else;
      };
      const prevPageClick = () => {
        pageNumber -= 1;
        leftElementOnPage -= cardsOnPage;
        document.querySelector('.button-tip-prev').classList.add('-visible');
        document.querySelector('.button-tip-prev').innerHTML = pageNumber;
        addButtonNumber();
        setTimeout(() => document.querySelector('.button-tip-prev').classList.remove('-visible'), 100);
      };

      if (arr.length === 0) {
        document.querySelector('.error-block').classList.add('-visible');
        document.querySelector('.error-block').innerHTML = 'Nothing found!';
      } else {
        maxPagesCalc();
        document.querySelector('.clip-container').classList.add('-visible');
        document.querySelector('.clip-container').style = `--clips-total: ${allElems}; --page: ${pageNumber};  --direction: 0rem`;

        arr.forEach((item) => {
          const clip = document.createElement('li');
          clip.className = 'clip';

          const clipCard = document.createElement('div');
          clipCard.className = 'clip-card';

          const clipPicture = document.createElement('img');
          clipPicture.className = 'clip-picture';
          clipPicture.src = `${item.pictureUrl}`;
          clipPicture.width = `${item.pictureWidth}`;
          clipPicture.alt = `${item.title}`;

          const clipTitle = document.createElement('div');
          clipTitle.className = 'clip-title';
          const clipLink = document.createElement('a');
          clipLink.className = 'clip-link';
          clipLink.href = `https://www.youtube.com/watch?v=${item.videoId}`;
          clipLink.target = '_blank';
          let sliced = item.title.slice(0, 50);
          if (sliced.length < item.title.length) {
            sliced += '...';
          }
          clipLink.innerHTML = `${sliced}`;
          clipTitle.appendChild(clipLink);

          const clipInfo = document.createElement('div');
          clipInfo.className = 'clip-info';
          const clipInfoWrapper = document.createElement('div');
          clipInfoWrapper.className = 'clip-info-wrapper';
          const clipInfoLine = document.createElement('div');
          clipInfoLine.className = 'clip-info-line';
          const clipInfoLineChannelTitle = document.createElement('div');
          clipInfoLineChannelTitle.className = 'clip-info-line channel-title';
          let slicedChannelTitle = item.channelTitle.slice(0, 13);
          if (slicedChannelTitle.length < item.channelTitle.length) {
            slicedChannelTitle += '...';
          }
          clipInfoLineChannelTitle.innerHTML = `${slicedChannelTitle}`;
          const clipInfoLineDate = document.createElement('div');
          clipInfoLineDate.className = 'clip-info-line date';
          const clipInfoLineDateTime = document.createElement('time');
          const fullDate = item.publishedAt;
          clipInfoLineDateTime.datetime = fullDate;
          clipInfoLineDateTime.innerHTML = `${fullDate.slice(0, fullDate.indexOf('T'))}`;
          clipInfoLineDate.appendChild(clipInfoLineDateTime);
          const clipInfoLineViewCount = document.createElement('div');
          clipInfoLineViewCount.className = 'clip-info-line view-count';
          clipInfoLineViewCount.innerHTML = `${item.viewCount}`;

          clipInfoWrapper.appendChild(clipInfoLineChannelTitle);
          clipInfoWrapper.appendChild(clipInfoLineDate);
          clipInfoWrapper.appendChild(clipInfoLineViewCount);

          const clipInfoDescription = document.createElement('div');
          clipInfoDescription.className = 'clip-description';
          clipInfoDescription.innerHTML = `${item.description}`;

          clipInfo.appendChild(clipInfoWrapper);
          clipInfo.appendChild(clipInfoDescription);

          document.querySelector('.clip-container').appendChild(clip);
          clip.appendChild(clipCard);
          clipCard.appendChild(clipPicture);
          clipCard.appendChild(clipTitle);
          clipCard.appendChild(clipInfo);
        });

        document.querySelector('.button-current').innerHTML = pageNumber + 1;
        document.querySelector('.navigation-bar').classList.remove('-hidden');
        document.querySelector('.button-current').classList.remove('-hidden');
        document.querySelector('.button-next').classList.remove('-hidden');

        window.onresize = resize;

        slider.onmousedown = down;
        slider.onmouseleave = leave;
        slider.onmousemove = move;
        slider.onmouseup = buttonUp;

        slider.addEventListener('touchstart', touchDown, false);
        slider.addEventListener('touchmove', touchMove, false);
        window.addEventListener('touchend', buttonUp, false);

        document.querySelector('.button-next').onclick = nextPageClick;
        document.querySelector('.button-prev').onclick = prevPageClick;
      }
    });
  }


  static removeItems() {
    if (document.querySelector('li') !== null) {
      document.querySelector('.clip-container').classList.remove('-visible');
      document.querySelector('.clip-container').removeAttribute = 'style';
      document.body.removeChild(document.querySelector('.media-check'));
      if (!document.querySelector('.button-prev').classList.contains('-hidden')) {
        document.querySelector('.button-prev').classList.add('-hidden');
      }
      while (document.querySelector('.clip-container').firstChild) {
        document.querySelector('.clip-container').removeChild(document.querySelector('.clip-container').firstChild);
      }
    } else if (document.querySelector('.error-block').firstChild !== null) {
      document.querySelector('.error-block').classList.remove('-visible');
      document.querySelector('.error-block').firstChild.remove();
    } else;
  }

  static secondRender(newdata) {
    newdata.then((database) => {
      const arr = database;
      const newArr = arr.splice(-15);
      newArr.forEach((item) => {
        const clip = document.createElement('li');
        clip.className = 'clip';

        const clipCard = document.createElement('div');
        clipCard.className = 'clip-card';

        const clipPicture = document.createElement('img');
        clipPicture.className = 'clip-picture';
        clipPicture.src = `${item.pictureUrl}`;
        clipPicture.width = `${item.pictureWidth}`;
        clipPicture.alt = `${item.title}`;

        const clipTitle = document.createElement('div');
        clipTitle.className = 'clip-title';
        const clipLink = document.createElement('a');
        clipLink.className = 'clip-link';
        clipLink.href = `https://www.youtube.com/watch?v=${item.videoId}`;
        clipLink.target = '_blank';
        let sliced = item.title.slice(0, 50);
        if (sliced.length < item.title.length) {
          sliced += '...';
        }
        clipLink.innerHTML = `${sliced}`;
        clipTitle.appendChild(clipLink);

        const clipInfo = document.createElement('div');
        clipInfo.className = 'clip-info';
        const clipInfoWrapper = document.createElement('div');
        clipInfoWrapper.className = 'clip-info-wrapper';
        const clipInfoLine = document.createElement('div');
        clipInfoLine.className = 'clip-info-line';
        const clipInfoLineChannelTitle = document.createElement('div');
        clipInfoLineChannelTitle.className = 'clip-info-line channel-title';
        let slicedChannelTitle = item.channelTitle.slice(0, 13);
        if (slicedChannelTitle.length < item.channelTitle.length) {
          slicedChannelTitle += '...';
        }
        clipInfoLineChannelTitle.innerHTML = `${slicedChannelTitle}`;
        const clipInfoLineDate = document.createElement('div');
        clipInfoLineDate.className = 'clip-info-line date';
        const clipInfoLineDateTime = document.createElement('time');
        const fullDate = item.publishedAt;
        clipInfoLineDateTime.datetime = fullDate;
        clipInfoLineDateTime.innerHTML = `${fullDate.slice(0, fullDate.indexOf('T'))}`;
        clipInfoLineDate.appendChild(clipInfoLineDateTime);
        const clipInfoLineViewCount = document.createElement('div');
        clipInfoLineViewCount.className = 'clip-info-line view-count';
        clipInfoLineViewCount.innerHTML = `${item.viewCount}`;

        clipInfoWrapper.appendChild(clipInfoLineChannelTitle);
        clipInfoWrapper.appendChild(clipInfoLineDate);
        clipInfoWrapper.appendChild(clipInfoLineViewCount);

        const clipInfoDescription = document.createElement('div');
        clipInfoDescription.className = 'clip-description';
        clipInfoDescription.innerHTML = `${item.description}`;

        clipInfo.appendChild(clipInfoWrapper);
        clipInfo.appendChild(clipInfoDescription);

        document.querySelector('.clip-container').appendChild(clip);
        clip.appendChild(clipCard);
        clipCard.appendChild(clipPicture);
        clipCard.appendChild(clipTitle);
        clipCard.appendChild(clipInfo);
      });
    });
  }
}
