import AppModel from '../models/AppModel';
import AppView from '../views/AppView';
import CreateUrl from '../models/CreateUrl';
import '../styles/style.css';

export default class App {
  static async start() {
    // const page = new AppView();
    AppView.addTagsToHead();
    AppView.drawBasicView();
    document.querySelector('.search-box-input').onkeyup = (e) => {
      const enterKeyCode = 13;
      if (e.keyCode === enterKeyCode) {
        e.preventDefault();
        AppView.removeItems();
        const searchString = document.querySelector('.search-box-input').value;
        const newUrl = new CreateUrl(searchString);
        const link = newUrl.getUrl(15);
        const clipsData = new AppModel(link);
        const data = clipsData.getData();
        AppView.renderItems(data);
        const target = document.querySelector('.media-check');
        const observer = new MutationObserver(() => {
          if ((getComputedStyle(target).width === '1200px' && target.innerHTML === '9' && document.querySelector('.clip-container').children.length === 15)
          || (getComputedStyle(target).width === '1024px' && target.innerHTML === '10' && document.querySelector('.clip-container').children.length === 15)
          || (getComputedStyle(target).width === '768px' && target.innerHTML === '13' && document.querySelector('.clip-container').children.length === 15)
          || (getComputedStyle(target).width === '480px' && target.innerHTML === '14' && document.querySelector('.clip-container').children.length === 15)) {
            const newlink = newUrl.getUrl(30);
            const newclipsData = new AppModel(newlink);
            const newdata = newclipsData.getData();
            AppView.secondRender(newdata);
          } else if ((getComputedStyle(target).width === '1200px' && target.innerHTML === '25' && document.querySelector('.clip-container').children.length === 30)
          || (getComputedStyle(target).width === '1024px' && target.innerHTML === '28' && document.querySelector('.clip-container').children.length === 30)
          || (getComputedStyle(target).width === '768px' && target.innerHTML === '27' && document.querySelector('.clip-container').children.length === 30)
          || (getComputedStyle(target).width === '480px' && target.innerHTML === '28' && document.querySelector('.clip-container').children.length === 30)) {
            const newlink = newUrl.getUrl(45);
            const newclipsData = new AppModel(newlink);
            const newdata = newclipsData.getData();
            AppView.secondRender(newdata);
          } else;
        });
        const config = { attributes: true, characterData: true };
        observer.observe(target, config);
      }
    };
  }
}
