export default class AppModel {
  constructor(link) {
    this.link = link;
  }

  async getData() {
    const responce = await fetch(this.link);
    const data = await responce.json();

    const allClipsIds = Array.from(data.items, clip => clip.id.videoId).join(',');
    const linkForId = `${this.link.slice(0, this.link.indexOf('search'))}videos${this.link.slice(this.link.indexOf('search') + 6, this.link.indexOf('type=video'))}&id=${allClipsIds}&part=snippet,statistics`;

    const responseId = await fetch(linkForId);
    const dataForViews = await responseId.json();

    const dataArray = Array.from(data.items, (obj) => {
      const {
        id: { videoId: videoId },
        snippet: {
          channelTitle: channelTitle,
          description: description,
          publishedAt: publishedAt,
          thumbnails: {
            medium: { url: pictureUrl, width: pictureWidth },
          },
          title: title,
        },
      } = obj;
      return {
        videoId, channelTitle, description, publishedAt, pictureUrl, pictureWidth, title,
      };
    });

    const idArray = Array.from(dataForViews.items, (obj) => {
      const {
        statistics: { viewCount: viewCount },
      } = obj;
      return { viewCount };
    });

    for (let i = 0; i < dataArray.length; i += 1) {
      Object.assign(dataArray[i], idArray[i]);
    }
    return dataArray;
  }
}
